.header {
  z-index: 4;
  width: 100%;
  background-color: var(--header-bg);
  border-bottom: 1px solid #6c6c6c;
  transition: transform .25s ease-in-out;
  position: fixed;
  top: 0;
  right: 0;
}

.header-link {
  align-items: center;
  display: flex;
}

.header-link:hover .header-link__text:hover:after {
  opacity: 1;
  transform: scale(1);
}

.header-link__logo {
  margin-right: 10px;
}

.header-link__text {
  font-size: var(--font-size20);
  text-transform: uppercase;
  -webkit-text-fill-color: transparent;
  background: linear-gradient(90deg, #709112 0%, rgba(128, 166, 20, .72) 48.96%, rgba(145, 189, 23, .59) 100%);
  -webkit-background-clip: text;
  background-clip: text;
  font-family: Kyiv Type Sans Bold2;
  transition: background-color .25s linear;
  position: relative;
}

.header-link__text:after {
  content: "";
  height: 1em;
  width: 110%;
  opacity: 0;
  -webkit-transition: opacity .5s, -webkit-transform .5s;
  border-bottom: 2px solid #709112;
  margin-top: 10px;
  transition: opacity .5s, transform .5s;
  display: inline-block;
  position: absolute;
  left: -5%;
  transform: scale(0, 1);
}

.header > .container {
  flex-direction: row;
  justify-content: space-between;
  padding-top: 5px;
  padding-bottom: 5px;
  display: flex;
}

.header.hide {
  transform: translateY(-50px);
}

.navigation {
  width: 50%;
  background-color: var(--header-bg);
  z-index: 10;
  border-bottom-left-radius: 6px;
  transition: transform .3s ease-in-out;
  position: absolute;
  top: 101.6%;
  right: 0%;
  transform: translateX(100%);
}

@media (min-width: 768px) {
